p {
  text-align: center;
  width: 110px;
  background-color: #fff;
  border-radius: 30px;
  padding: 10px 15px;
  font-size: 20px;
  font-weight: 500;
  display: block;
}

.button-wrap {
  width: 350px;
  justify-content: space-between;
  margin: auto auto auto 50px;
  display: flex;
}

button[data-start] {
  background-color: #7cd87b;
}

button[data-stop] {
  background-color: #e24c4c;
}

button[data-start], button[data-stop] {
  border-radius: 50px;
  padding: 15px 40px;
  font-size: 24px;
  font-weight: 700;
}

.color-output {
  text-align: center;
  width: 280px;
  background-color: rgba(255, 255, 255, .4);
  border-radius: 30px;
  margin: 20px auto auto 100px;
  font-size: 30px;
  font-weight: 700;
  line-height: 1.5;
}

